let Rx = require('rx');
let u = require('dpaw-brocket-utility');

let Obs = Rx.Observable;
let log = u.log;

function model(actions, incoming$) {
  log('App.model: got actions', actions);

  let click$ = actions.click$
        .startWith({});

  let position$ = actions.mapClick$
        .do(evt => log('App.model.map-clicks: got click', evt))
        .map(evt => evt.latlng)
        .scan(gatherPositions, [])
        .do( arr => log('Positions are ', arr))
        .startWith([{a: 1}]);
  

  let messageArr$ = incoming$
        .startWith([{tag: 'init', payload: 'in the beginning'}])
        .pluck('payload')
        .scan(myAggregate, []);

  let state$ = Rx.Observable.combineLatest(
    messageArr$, position$,
    (messageArr, clicks) => ({
      messageArr,
      clicks
    })
  );
  log('App.model: state$ is ', state$);
  return state$;
}

module.exports = model;

function myAggregate(acc, val, ix, src) {
  acc.push(val);
  return acc.slice(-10);
}

function gatherPositions(acc, val, ix, src) {
  log('gatherPositions: got val');
  acc.push(val);
  return acc;
}



