
let Cycle = require('@cycle/core');
let CycleDOM = require('@cycle/dom');
let Rx = require('rx');

let u = require('dpaw-brocket-utility');
let Obs = Rx.Observable;
let log = u.log;

let h = CycleDOM.h;

function view({state$, admin$, incoming$, leaflet$}) {
  log('App.view: state$ is', state$);
  log('App.view: admin$ is', admin$);
  log('App.view: leaflet$ is', leaflet$);
  let vtree$ = Obs.combineLatest(
    state$, admin$, incoming$, leaflet$, render);
  return vtree$;
}

function render(state, admin, incoming, leaflet) {
  log('App.view.render: got positions', state.clicks);
  log('App.view.render: got leaflet', leaflet);
  let view = h('div', [
    leaflet,  // leaf.vtree
    h('div#admin', [
      h('h2', 'Admin goes here'),
      admin
    ]),
    h('div.log-container', [
      h('div', [incoming]),
    ]),
    // h('div.positions-container', [
    //   h('h4', 'Positions'),
    //   h('ul', state.clicks.map( val => h('li', `lat: ${val.lat} - lng: ${val.lng}`)))
    // ])
  ]);
  
  // let view = h('#app-container', [
  //   h('div#map-container', [leaflet]),
  //   h('div#admin', [admin]),
  //   h('div.log-container', [
  //     h('div', [incoming])
  //   ])
  // ]);
  return view;
};

module.exports = view;



