let Rx = require('rx');
let u = require('dpaw-brocket-utility');

let Obs = Rx.Observable;
let log = u.log;

function intent(DOM, incoming$) {
  console.log('intent: relay incoming is ', incoming$);
  return {
    incoming$:    incoming$,
    mapClick$:    DOM.select('#map').events('map-click').map(evt => evt.detail),
    click$:       DOM.select('#app').events('click')
  };
}

module.exports = intent;



